<template>
	<div class="payPhoneBill">
		<div class="payPhoneBill_hrad">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p>充值订单</p>
		</div>
		<p class="yearMonth" @click="showPicker"><span>{{ YearMonth }}</span><i class="icon_arrow_bottom"></i></p>
		<ul class="payPhoneBill_list">
			<li v-for="item in payPhoneBillList" :key="item.orderNo" @click="goPhoneBillDetails(item)">
				<p class="payPhoneBill_list_title">订单状态：<span>{{ item.rechargeStatus }}</span></p>
				<div class="payPhoneBill_list_box">
					<img src="../static/img/icon_50.jpg" alt="" class="icon_number" v-if="item.actualAmount == 50">
					<img src="../static/img/icon_100.jpg" alt="" class="icon_number" v-if="item.actualAmount == 100">
					<img src="../static/img/icon_200.jpg" alt="" class="icon_number" v-if="item.actualAmount == 200">
					<div class="payPhoneBill_list_info">
						<p class="text1">{{ item.actualAmount }}元话费充值</p>
						<p class="text2">下单时间：{{ new Date(item.createTime).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-') }}</p>
						<p class="text3">实际支付：<span>¥{{ item.payAmount }}</span></p>
					</div>
				</div>
			</li>
		</ul>
		<div v-show="payPhoneBillList.length == 0">
		  <van-empty description="暂无记录" />
		</div>
		<div class="shadow" v-show="shadowFlag">
		  <div class="picker_box">
			<van-datetime-picker
			v-model="currentDate"
			type="year-month"
			title="选择年月"
			:min-date="minDate"
			:max-date="maxDate"
			@confirm="confirm"
			@cancel="cancel"
			:formatter="formatter"
			/>
		  </div>
		</div>
	</div>
</template>

<script>
	export default {
		name:'payPhoneBill',
		data(){
			return {
				obj:{},
				payPhoneBillList:[],
				YearMonth:'2021-08',
				shadowFlag:false,
				minDate: new Date(2018, 0, 1),
				maxDate: new Date(2021, 11, 1),
				currentDate: new Date(),
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.rechargeList();
		},
		methods:{
			goBack(){
			  this.$router.push({path:'/payPhone'});
			},
			formatter(type, val) {
			  if (type === 'year') {
				return `${val}年`;
			  } else if (type === 'month') {
				return `${val}月`;
			  }
			  return val;
			},
			confirm(e){
				var YearMonth = e.getFullYear()+'-'+(e.getMonth()+1 < 10 ? '0'+(e.getMonth()+1) : e.getMonth()+1);
				this.YearMonth = YearMonth;
				this.shadowFlag = false;
				this.rechargeList();
			},
			cancel(e){
				this.shadowFlag = false;
			},
			showPicker(){
				this.shadowFlag = true;
			},
			rechargeList(){
			  let _this = this;
			  var ss = 'dateTime='+_this.YearMonth+'&pageSize=100&mediaCode='+ _this.obj.mediaCode +'&pageNum=0&userId='+_this.obj.userId;
			  var data = _this.byte256.byte256.encode(_this.key,ss);
			  _this.$axios({
				method: 'get',
				url: '/recharge/rechargeList?params='+encodeURIComponent(data),
			  }).then(function(res) {
				console.log('rechargeList======',res);
				if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.payPhoneBillList = reData.rows;
				}
			  });
			},
			goPhoneBillDetails(item){
				this.$router.push({path:'/phoneBillDetails',query:{item:item}})
			},
		}
	}
</script>

<style scoped>
	.payPhoneBill{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #f4f4f4;
		box-sizing: border-box;
	}
	.payPhoneBill_hrad{
		width: 100%;
		height: 2.32rem;
		box-sizing: border-box;
		padding:var(--barHeight) 0.33rem 0 0.33rem;
		background-color: #ff2c63;
		position: relative;
	}
	.icon_arrow_left{
		display: inline-block;
		vertical-align: middle;
		width:0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left.png);
		background-repeat: no-repeat;
		background-size: 100%;
		position: absolute;
		left:0.33rem;
		top:1.275rem;
		z-index: 3;
	}
	.payPhoneBill_hrad p{
		width: 100%;
		height: 1.51rem;
		line-height: 1.51rem;
		position: absolute;
		top:0.8rem;
		left:0;
		z-index: 2;
		text-align: center;
		color:#fff;
		font-size: 0.48rem;
	}
	.payPhoneBill_list{
		width:100%;
		box-sizing: border-box;
		padding:0.32rem;
	}
	.payPhoneBill_list li{
		width: 100%;
		background-color: #fff;
		border-radius: 0.3rem;
		box-sizing: border-box;
		padding: 0 0.1rem;
		margin-bottom:0.3rem;
	}
	.payPhoneBill_list_title{
		width: 100%;
		height:0.98rem;
		line-height: 0.98rem;
		border-bottom:0.02rem solid #d2d2d2;
		font-size: 0.36rem;
		color:#232426;
		padding-left:0.32rem;
		font-weight: 600;
		box-sizing: border-box;
	}
	.payPhoneBill_list_box{
		box-sizing: border-box;
		padding:0.32rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.icon_number{
		width: 1.8rem;
		height:1.8rem;
		
	}
	.payPhoneBill_list_info{
		width:7.2rem;
		line-height: 0.64rem;
	}
	.payPhoneBill_list_info .text1{
		font-size: 0.48rem;
		color:#232426;
	}
	.payPhoneBill_list_info .text2{
		font-size: 0.36rem;
		color:#848383;
	}
	.payPhoneBill_list_info .text3{
		font-size: 0.36rem;
		color:#848383;
	}
	.payPhoneBill_list_info .text3 span{
		font-size: 0.48rem;
		color:#ff2c63;
	}
	.picker_box{
		width: 10.8rem;
		position: fixed;
		left:0;
		bottom:0;
		z-index: 100;
	}
	.yearMonth{
		font-size: 0.48rem;
		color: #000;
		line-height: 1.64rem;
		width: 10.8rem;
		height: 1.64rem;
		box-sizing: border-box;
		padding:0 0.64rem;
	}
	.yearMonth span{
		display: inline-block;
		vertical-align: middle;
	}
	.icon_arrow_bottom{
		display: inline-block;
		vertical-align: middle;
		width: 0.26rem;
		height:0.16rem;
		background-image: url(../static/img/icon_arrow_bottom.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-left:0.3rem;
	}
</style>
